import styles from './SidebarOderSummary.module.scss';
import classNames from 'classnames/bind';
import { CartState } from '../../store/Context';
import { useState } from 'react';

const cx = classNames.bind(styles);

function SidebarOderSummary() {
  const {state:{total},dispatch} = CartState();
  const [isChecked,setIsChecked] = useState(false); 
  const [errorMsg,setErrorMsg] = useState('');

    const handlePayment = (e) => {
      console.log(e.target.checked)
      if(!e.target.checked) {
        setIsChecked(false)
      } else {
        setErrorMsg('');
        setIsChecked(true);
        if(e.target.value==="money") {
          dispatch({type:"ADDPAYMENTMETHOD",payload:"Tin mt"})
        } else {
          dispatch({type:"ADDPAYMENTMETHOD",payload:"Th tn dng"})
        }
      }
    }

    return (
      <>
        <h3 className={cx("title")}>Tng ha n</h3>
        <div className={cx("content")}>
          <div className={cx('wrap')}>
            <span className={cx('subtotal')}>Tng ph</span>
            <span className={cx('subtotal-price')}>{total.toLocaleString()}.000 VND</span>
          </div>
          <div className={cx('wrapper')}>
            <span>Phng thc thanh ton</span>
              <br/>
              <br/>
              <input type='radio' id="money" name="method" value="money" onClick={handlePayment}/>
              <label htmlFor="money">Tin mt</label><br/><br/>
              <input type='radio' id="credit" name="method" value="credit" onClick={handlePayment}/>
              <label htmlFor="credit">Th tn dng</label><br/>
              {errorMsg&&<p className={cx('message-error')}>{errorMsg}</p>}
          </div>
          <div className={cx('wrap')}>
            <span className={cx('name')}>Giao hng</span>
            <span className={cx('free')}>FREE</span>
          </div>
          <div className={cx('wrap-total')}>
            <span className={cx('total')}>Tng</span>
            <span className={cx('total-price')}>{total.toLocaleString()}.000 VND</span>
          </div>
          <button className={cx('btn-checkout')} onClick={() => {isChecked?dispatch({type:"SHOWMESSAGE",payload:true}):dispatch({type:"SHOWMESSAGE",payload:false});setErrorMsg('Bn cn phi chn phng thc thanh ton');}}>Thanh ton</button>
        </div>
      </>
    );  
}

export default SidebarOderSummary;