import styles from './ModalCheckOut.module.scss';
import classNames from 'classnames/bind';
import { CartState } from '../../store/Context';
import { useEffect, useState } from 'react';
import { AuthState } from '../../store/AuthProvider';
import { Link } from 'react-router-dom';
import { url } from '../../constants/index';
import axios from 'axios';
import { MessageState } from '../../store/MessageContext';

const cx = classNames.bind(styles);

function ModalCheckOut() {
    const {state:{isCheckOut,products,paymentMethod,total},dispatch} = CartState();
    const {user} = AuthState();
    const initialValues = {fullname:user?.user.fullname,email:user?.user.email,address:user?.user.address,phone:user?.user.phone}
    const [formValues,setFormValues] = useState(initialValues);
    const [formErrors,setFormErrors] = useState({});
    const {modalSuccess,setModalSuccess} = MessageState();
    //const [success,setSuccess] = useState(false);
    const date = new Date();

    const handleChange = (e) => {
      const {name,value} = e.target;
      setFormValues({...formValues,[name]:value});
    }

    const validate = (values) => {
      const msg = {};
      const regx = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
      if(!values.fullname) {
        msg.fullname = 'H tn l bt buc';
      }
      if(!values.email) {
        msg.email = 'Email l bt buc';
      } else if(!regx.test(values.email)) {
        msg.email = 'Email khng hp l';
      }
      if(!values.address) {
        msg.address = 'a ch l bt buc';
      }
      if(!values.phone) {
        msg.phone = 'S in thoi l bt buc';
      }
      return msg;
    }

    const handleSubmit = async(e) => {
      e.preventDefault();
      setFormErrors(validate(formValues));
      try {
        if(formValues.fullname!==''&&formValues.address!==''&&formValues.email!==''&&formValues.phone!=='') {
          await axios.post(url+'/orders',{orderDate:date,status:"ang vn chuyn",total:total,paymentMethod:paymentMethod},{
            headers: {
              'Authorization': `Bearer ${user.token}`
            }
          })
          setModalSuccess(true);
          dispatch({type:"CLEARCARTBUTNOTCLOSEMESSAGE",payload:isCheckOut});
        } else {
          setModalSuccess(false);
        }
      } catch(e) {
        throw new Error(e);
      }
    }

    return (
      <>
        {user!==null?<>
        
        {isCheckOut && (
          <div
            className={cx("modal")}
            onClick={(e) => {
              dispatch({ type: "SHOWMESSAGE", payload: false });
              e.stopPropagation();
            }}
          ></div>
        )}
        {isCheckOut&& (
          <div className={cx("modal-content")}>
            {modalSuccess === false ?<>
              <h3 className={cx("modal-title")}>Xc nhn thng tin</h3>
              <div className={cx("modal-body")}>
                <form className={cx("modal-form")} onSubmit={handleSubmit}>
                  <div className={cx("modal-form-wrap")}>
                    <div className={cx("form-group-left")}>
                      <div className={cx("modal-form-group")}>
                        <span className={cx("form-group-title")}>H tn</span>
                        <input
                          type="text"
                          name="fullname"
                          placeholder="H tn"
                          value={formValues.fullname}
                          onChange={handleChange}
                          className={cx("modal-form-input")}
                          autoComplete="off"
                        />
                        {formErrors.name && (
                          <p className={cx("message")}>{formErrors.fullname}</p>
                        )}
                      </div>
                      <div className={cx("modal-form-group")}>
                        <span className={cx("form-group-title")}>Email</span>
                        <input
                          type="text"
                          name="email"
                          placeholder="Email"
                          value={formValues.email}
                          onChange={handleChange}
                          className={cx("modal-form-input")}
                          autoComplete="off"
                        />
                        {formErrors.email && (
                          <p className={cx("message")}>{formErrors.email}</p>
                        )}
                      </div>
                    </div>
                    <div className={cx("form-group-right")}>
                      <div className={cx("modal-form-group")}>
                        <span className={cx("form-group-title")}>a ch</span>
                        <input
                          type="text"
                          name="address"
                          placeholder="a ch"
                          value={formValues.address}
                          onChange={handleChange}
                          className={cx("modal-form-input")}
                          autoComplete="off"
                        />
                        {formErrors.address && (
                          <p className={cx("message")}>{formErrors.address}</p>
                        )}
                      </div>
                      <div className={cx("modal-form-group")}>
                        <span className={cx("form-group-title")}>S in thoi</span>
                        <input
                          type="text"
                          name="phone"
                          placeholder="S in thoi"
                          value={formValues.phone}
                          onChange={handleChange}
                          className={cx("modal-form-input")}
                          autoComplete="off"
                        />
                        {formErrors.phone && (
                          <p className={cx("message")}>{formErrors.phone}</p>
                        )}
                      </div>
                    </div>
                  </div>
                  <div className={cx("right")}>
                    <button type="submit" className={cx("btn-submit")}>
                      Xc nhn
                    </button>
                  </div>
                </form>
              </div>
              <button
                className={cx("btn-close")}
                onClick={(e) => {
                  dispatch({ type: "SHOWMESSAGE", payload: false });
                  e.stopPropagation();
                }}
              >
                &times;
              </button>
            </>:<>
              <h3 className={cx("modal-title")}>Cm n v  mua sn phm ca chng ti</h3>
              <div className={cx('model-content')}>Vui lng kim tra email ca bn  nhn hng</div>
              <button
                className={cx("btn-close")}
                onClick={(e) => {
                  dispatch({ type: "SHOWMESSAGE", payload: false });
                  e.stopPropagation();
                }}
              >
                &times;
              </button>
            </>}
          </div>
        )}
        </>:<>
        {isCheckOut && (
          <div
            className={cx("modal")}
            onClick={(e) => {
              dispatch({ type: "SHOWMESSAGE", payload: false });
              e.stopPropagation();
            }}
          ></div>
        )}
        {isCheckOut && (
          <div className={cx('modal-content')}>
            <h3 className={cx("modal-title")}>Bn phi ng nhp  thanh ton</h3>
            <Link to="/login" onClick={(e) => {dispatch({ type: "SHOWMESSAGE", payload: false });e.stopPropagation();}}>ng nhp ti y</Link>
            <button
                className={cx("btn-close")}
                onClick={(e) => {
                  dispatch({ type: "SHOWMESSAGE", payload: false });
                  e.stopPropagation();
                }}
              >
                &times;
              </button>
          </div>
        )}
        </>}
      </>
    );
}

export default ModalCheckOut;